body {
  background-color: #f8f8f8;
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2rem;
}

[class$=contenedor],
.contenedor {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.seccion {
  padding: 2rem 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.2rem;
}

h3 {
  font-size: 2.4rem;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
}

.btn {
  background-color: #FFFFFF;
  padding: 2rem 2rem;
  color: #000;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .btn {
    width: auto;
  }
}
.btn:hover {
  background-color: #e6e6e6;
}

.boton-negro {
  background-color: #000;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
}
@media (min-width: 768px) {
  .boton-negro {
    width: auto;
  }
}
.boton-negro:hover {
  background-color: black;
}

.header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 250px;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
    gap: 2rem;
  }
}
.navegacion:last-of-type {
  margin: 0;
}
.navegacion__enlace {
  color: #000;
  font-size: 1.8rem;
  font-weight: 700;
}
.navegacion__enlace--principal {
  font-weight: 700;
  color: #6DB562;
}

.bienvenida {
  text-align: center;
  padding: 30px;
}
.bienvenida__heading {
  margin-top: 0;
  text-align: center;
  color: #6DB562;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  line-height: 0.9;
  text-transform: lowercase;
  font-size: 4rem;
}
.bienvenida__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
.bienvenida__texto {
  font-size: 2.6rem;
  color: #979797;
  max-width: 40rem;
  margin: 4rem auto 0 auto;
  margin-top: 10px;
}

.tarjetas {
  padding: 30px;
}
@media (min-width: 768px) {
  .tarjetas__grilla {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.tarjetas__texto {
  text-align: center;
  padding: 15px;
  margin-top: 0px;
}

.tarjeta {
  max-width: 30rem;
  margin: 0 auto 2rem auto;
}
.tarjeta:last-of-type {
  margin-bottom: 0;
}
@media (min-width: 768px) {
  .tarjeta {
    max-width: unset;
    margin: 0;
  }
}
.tarjeta__imagen {
  width: 100%;
  border: 0.5rem solid #FFFFFF;
  transition-property: rotate scale;
  transition-duration: 300ms;
}
.tarjeta__imagen:hover {
  transform: rotate(4deg) scale(0.7);
}
.tarjeta__contenido {
  max-width: 90%;
  margin: -5rem auto 0 auto;
  background-color: #FFFFFF;
  position: relative;
}
.tarjeta__heading {
  text-align: center;
  color: #6DB562;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  line-height: 0.9;
  text-transform: lowercase;
  font-size: 4rem;
  font-size: 3.4rem;
  padding: 0.4rem 1rem;
}
.tarjeta__heading span {
  color: #000;
  display: block;
  text-transform: uppercase;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
.tarjeta__enlace {
  background-color: #6DB562;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  padding: 12px;
  display: block;
}
@media (min-width: 768px) {
  .tarjeta__enlace {
    width: auto;
  }
}
.tarjeta__enlace:hover {
  background-color: #549b49;
}
.tarjeta__texto {
  text-align: center;
  max-width: 120rem;
  margin: 2rem auto 0 auto;
  font-weight: 600;
}

.solicitud {
  margin: 3rem 0;
  background-image: url(../img/bg.jpg);
  background-position: center center;
  background-size: cover;
  padding: 6rem 0rem;
  color: #FFFFFF;
  text-align: center;
  background-attachment: fixed;
}
.solicitud__heading {
  text-transform: uppercase;
  display: block;
  text-align: center;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
}
.solicitud__texto {
  font-weight: 300;
  max-width: 60rem;
  margin: 0 auto;
  color: #FFFFFF;
  line-height: 2;
}
.solicitud__enlace {
  background-color: #6DB562;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  padding: 12px 15px;
}
@media (min-width: 768px) {
  .solicitud__enlace {
    width: auto;
  }
}
.solicitud__enlace:hover {
  background-color: #549b49;
}

.slider {
  overflow: hidden;
}
.slider__slide {
  cursor: pointer;
  height: 100%;
  flex-shrink: 0;
  width: 100px;
  position: relative;
  transition-property: transform;
}
.slider .swiper-button-next,
.slider .swiper-button-prev {
  color: #FFFFFF;
}
.slider .swiper-pagination-bullet-active {
  background-color: #6DB562;
}

.footer {
  padding: 20px;
}
@media (min-width: 768px) {
  .footer__grilla {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__caja {
  text-align: left;
  text-align: center;
}
.footer__heading {
  color: #6DB562;
  text-align: center;
}
.footer__texto {
  color: #000;
  font-weight: 600;
  font-size: 2rem;
  text-align: center;
  line-height: 1.8;
}
.footer__derechos {
  text-align: center;
  max-width: 120rem;
  margin: 2rem auto 0 auto;
  border-top: 1px solid #dfdfdf;
  font-weight: 600;
}
.footer__derechos span {
  color: #6DB562;
}

.header {
  display: flex;
  flex-direction: column;
  padding: 5rem 0;
  align-items: center;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__imagen {
  width: 30rem;
}

.navegacion {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  width: 100%;
}
@media (min-width: 1024px) {
  .navegacion {
    width: auto;
    flex-direction: row;
  }
}
.navegacion__link {
  font-size: 2rem;
  font-weight: 700;
  color: #000;
}
.navegacion__link--ayuda {
  background-color: #6DB562;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  text-transform: capitalize;
  padding: 0.8rem 3rem;
  font-size: 2rem;
  margin-top: 0rem;
}
@media (min-width: 768px) {
  .navegacion__link--ayuda {
    width: auto;
  }
}
.navegacion__link--ayuda:hover {
  background-color: #549b49;
}

@media (min-width: 768px) {
  .formulario__grilla {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.formulario__imagen {
  margin-top: 0rem;
  display: flex;
  justify-content: center;
}
.formulario__img {
  width: 40rem;
}
@media (min-width: 768px) {
  .formulario__img {
    width: 100%;
  }
}
.formulario__contenido {
  background-color: #f8f8f8;
  padding: 0rem;
}
.formulario__heading {
  color: #000;
  text-align: center;
  text-transform: uppercase;
}
.formulario__heading span {
  color: #6DB562;
}
.formulario__campo {
  margin-bottom: 1rem;
}
.formulario__label {
  display: block;
  text-transform: uppercase;
  color: #000;
  margin: 1rem 0;
  font-weight: 600;
}
.formulario__label--olvido {
  display: block;
  margin: 0.7rem 0;
  color: #000;
  font-weight: 900;
}
.formulario__input {
  width: 100%;
  padding: 1.2rem;
  border-radius: 5px;
}
.formulario__submit {
  background-color: #6DB562;
  padding: 2rem 2rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2.2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: background-color;
  transition-duration: 300ms;
  padding: 0.8rem;
  border-radius: 5px;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: auto;
  }
}
.formulario__submit:hover {
  background-color: #549b49;
}
@media (min-width: 768px) {
  .formulario__submit {
    width: 100%;
  }
}

